export const SIZE = {
	small: () => `
		font-size: 1rem;
		padding: 1rem 1rem;
	`,
	medium: () => `
		font-size: 1rem;
		padding: .5rem 1rem;
	`,
	large: () => `
		font-size: 1.4rem;
		padding: .7rem 1.4rem;
	`,
};
export const getSize = size => SIZE[size] || SIZE.medium;

export const SHAPE = {
	'square': () => `
		border-radius: 0;
	`,
	'round': () => `
		border-radius: 5px;
	`,
	'circle': () => `
		border-radius: 50%;
	`,
}
export const getShape = shape => SHAPE[shape] || SHAPE.round;

// B6C9D4
// E5E5DD
export const PALETTE = {
	'primary': () => `
		--textColor: #FFF;
		--backgroundColor: #424743;
		--borderColor: #424743;
		--paleTextColor: #424743;
		--paleBackgroundColor: #4247433b;
		--paleBorderColor: #4247433b;
	`,
	'secondary': () => `
		--textColor: #FFF;
		--backgroundColor: #595F6E;
		--borderColor: #595F6E;
		--paleTextColor: #595F6E;
		--paleBackgroundColor: #595F6E3b;
		--paleBorderColor: #595F6E3b;
	`,
	'tertiary': () => `
		--textColor: #FFF;
		--backgroundColor: #738693;
		--borderColor: #738693;
		--paleTextColor: #738693;
		--paleBackgroundColor: #7386933b;
		--paleBorderColor: #7386933b;
	`,
}
export const getPalette = palette => PALETTE[palette] || PALETTE.secondary;

export const MODEL = {
	'normal': () => `
		color: var(--textColor);
		background-color: var(--backgroundColor);
		border-color: var(--borderColor);
	`,
	'shade': () => `
		color: var(--paleTextColor);
		background-color: var(--paleBackgroundColor);
		border-color: var(--paleBorderColor);
	`,
	'ghost': () => `
		color: var(--backgroundColor);
		background-color: transparent;
		border-color: var(--backgroundColor);
	`,
	'text': () => `
		color: var(--backgroundColor);
		background-color: transparent;
		border-color: transparent;
	`,
	'link': () => `
		color: var(--backgroundColor);
		background-color: transparent;
		border-color: transparent;
		text-decoration: none;
	`,
}
export const getModel = model => MODEL[model] || MODEL.normal;

