import React, { useState } from 'react';

import {
	LoadingOutlined,
} from '@ant-design/icons';

import {
	Styled,
	Option,
	Title,
	SubTitle,
} from './style';

function Radio({
	size,
	shape,
	palette,
	model,
	loading = false,
	children,
	options,
	defaultIndex = -1,
}) {
	const [selectedIndex, setSelectedIndex] = useState(defaultIndex);

	return (
		<Styled
			size={size}
			shape={shape}
		>
			{options.map((e, i) => (
				<Option
					key={e.value}
					size={size}
					shape={shape}
					palette={palette}
					model={model}
					selected={i === selectedIndex}
					onClick={() => setSelectedIndex(i)}
				>
					<Title>{e.title}</Title>
					{e.subtitle && (<SubTitle>{e.subtitle}</SubTitle>)}
				</Option>
			))}
		</Styled>
	);
}

export default Radio;
