import React, { useState } from 'react';
import { useParams } from 'react-router-dom';

import {
	Styled,
	Link,
	LinkReadOnly,
	Separator,
} from './style';

const LINKS = true ? [] : [
	{ path: '/path1', name: 'Name 1', },
	{ path: '/path2', name: 'Name 2', },
	{ path: '/path3', name: 'Name 3', },
];

const CATEGORIES = [
	{
		name: 'Кофе',
		path: '/app/category/coffee',
	},
	{
		name: 'Чай',
		path: '/app/category/tea',
	},
	{
		name: 'Пончики',
		path: '/app/category/donuts',
	},
	{
		name: 'Выпечка',
		path: '/app/category/bakery',
	},
	{
		name: 'Разное',
		path: '/app/category/other',
	},
	{
		name: 'Сезонное',
		path: '/app/category/season',
	},
	{
		name: 'Экстра',
		path: '/app/category/extra',
	},
	{
		name: 'Акции',
		path: '/app/category/action',
	},
];

function Breadcrumbs() {
	const { id } = useParams();
	const category = CATEGORIES.find(e => e.path.endsWith(id));

	return (
		<Styled hidden={!category}>
			{category && (
				<>
					<Link to="/app/category">Меню</Link>
					<Separator>{' > '}</Separator>
					<LinkReadOnly>{category.name}</LinkReadOnly>
				</>
			)}
		</Styled>
	);
}

export default Breadcrumbs;
