import React, { useState } from 'react';

import {
	MenuOutlined,
	ShoppingCartOutlined,
} from '@ant-design/icons';

import {
	Styled,
	Info,
	CartStats,
	Sum,
	SumValue,
	Currency,
	Selected,
	SelectedCounter,
	CartIcon,
	Action,
} from './style';

function Header() {
	const [items, setItems] = useState([]);

	return (
		<Styled>
			<Action to='/app'>
				<MenuOutlined />
			</Action>
			<Info to="/app">
				{items.length > 0 && (
					<CartStats>
						<Sum>
							<SumValue>0</SumValue>
							{'\u00a0'}
							<Currency>руб</Currency>
						</Sum>
						<Selected>
							Выбрано:
							{'\u0020'}
							<SelectedCounter>{items.length}</SelectedCounter>
						</Selected>
					</CartStats>
				)}
				<CartIcon>
					<ShoppingCartOutlined />
				</CartIcon>
			</Info>
		</Styled>
	);
}

export default Header;
