function Menu() {
	const [category, setCategory] = useState(null);
	const [data, setData] = useState([]);
	const [isLoading, setIsLoading] = useState(false);
	const location = useLocation();

	useEffect(() => {
		setIsLoading(true);
		setTimeout(() => {
			setIsLoading(false);
			setData(CATEGORIES);
		}, 2000)
	}, []);

	function onClick(e) {
	}

	useEffect(() => {
		setCategory(CATEGORIES.find(e => location.pathname.endsWith(e.path)));
	}, [location]);

	return (
		<Switch>
			{isLoading && (
				<Styled>
					<Loader>
						<LoadingOutlined />
						<LoaderText>Данные загружаются</LoaderText>
					</Loader>
				</Styled>
			)}
			<Route exact path="/app">
				{!isLoading && (
					<Styled>
						<Breadcrumbs />
						{data.length === 0 ? (
							<EmptyMessage>
								<EmptyMessageIcon>
									<InfoCircleOutlined />
								</EmptyMessageIcon>
								<EmptyMessageTitle>
									Здесь совсем пусто
								</EmptyMessageTitle>
							</EmptyMessage>
						) : (
							<C>
								<Content>
									{data.map(e => (
										<Item key={e.path} to={e.path} onClick={onClick}>
											<ItemPlaceholder />
											<ItemName>{e.name}</ItemName>
										</Item>
									))}
								</Content>
							</C>
						)}
					</Styled>
				)}
			</Route>
			<Route exact path="/app/category">
				{!isLoading && (
					<Styled>
						<Breadcrumbs />
						{data.length === 0 ? (
							<EmptyMessage>
								<EmptyMessageIcon>
									<InfoCircleOutlined />
								</EmptyMessageIcon>
								<EmptyMessageTitle>
									Здесь совсем пусто
								</EmptyMessageTitle>
							</EmptyMessage>
						) : (
							<C>
								<Content>
									{data.map(e => (
										<Item key={e.path} to={e.path} onClick={onClick}>
											<ItemPlaceholder />
											<ItemName>{e.name}</ItemName>
										</Item>
									))}
								</Content>
							</C>
						)}
					</Styled>
				)}
			</Route>
			<Route path="/app/category/:id">
				<Styled>
					<Breadcrumbs />
					<C>
						{category && category.items.length > 0 ? (
							<ContentP>
								{category.items.map(e => (
									<Product key={e.path} to={e.path}>
										<ProductHeader>
											<ProductImage />
											{(Array.isArray(e.variations) && e.variations.length > 0) ? (
												<ProductName>{e.name}</ProductName>
											) : (
												<>
													<ProductName>{e.name} ({e.volume} мл)</ProductName>
													<ProductPrice>{e.price} руб</ProductPrice>
												</>
											)}
										</ProductHeader>
										<ProductFooter>
											{Array.isArray(e.variations) && e.variations.length > 0 ? (
												<Radio
													palette="primary"
													model="ghost"
													options={e.variations.map(e => ({ title: `${e.volume}мл`, subtitle: `${e.price} руб`, value: e.price }))}
												/>
											) : (
												<Button>Добавить в корзину</Button>
											)}
										</ProductFooter>
									</Product>
								))}
							</ContentP>
						) : (
							<EmptyMessage>
								<EmptyMessageIcon>
									<InfoCircleOutlined />
								</EmptyMessageIcon>
								<EmptyMessageTitle>
									Здесь совсем пусто
								</EmptyMessageTitle>
							</EmptyMessage>
						)}
					</C>
				</Styled>
			</Route>
		</Switch>
	);
}

export default Menu;
