import React, { useEffect } from 'react';
import Main from 'page/Main';

import {
	HashRouter as Router,
	Switch,
	Route,
} from 'react-router-dom';

function App() {
	useEffect(() => {
		document.documentElement.style.setProperty('--vh', `${window.innerHeight/100}px`);
	}, []);

	return (
		<Router>
			<Switch>
				<Route exact path="/">
					<Main />
				</Route>
				<Route path="/app">
					<Main />
				</Route>
			</Switch>
		</Router>
	);
}

export default App;
